<template>
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/organization" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>组织机构管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="index">
      <div class="basic">
        <div>
          <p>{{item.title}}</p>
          <div></div>
        </div>
      </div>
      <div class="chairman">
        <div v-for="(s,index) in item.lists" :key="index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          title: "工会基本信息",
          lists: [
            {
              p: "工会名称",
              span: "青海省总工会"
            },
            {
              p: "级别",
              span: "一级"
            },
            {
              p: "上级工会名称",
              span: "无"
            },
            {
              p: "联系人",
              span: "吴鹏德"
            },
            {
              p: "联系方式",
              span: "0971-8546999"
            },
            {
              p: "联系地址",
              span: "西宁市虎台路21号"
            },
            {
              p: "成立时间",
              span: "2019-02-06"
            },
            {
              p: "企业人数",
              span: "3000人"
            },
            {
              p: "会员人数",
              span: "300人"
            },
            {
              p: "工会届次",
              span: "第二届"
            },
            {
              p: "工资集体协商",
              span: "已签订"
            }
          ]
        },
        {
          title: "工会主席信息",
          lists: [
            {
              p: "主席姓名",
              span: "张萌"
            },
            {
              p: "职务",
              span: "主席"
            },
            {
              p: "身份证号",
              span: "632126199801095563"
            },
            {
              p: "所获荣誉",
              span: "全国劳模"
            },
            {
              p: "联系电话",
              span: "15569712036"
            },
            {
              p: "本届工会班子是否经会员代表大会选举产生",
              span: "是"
            },
            {
              p: "本届工会班子经何种方式产生",
              span: "推荐"
            }
          ]
        }
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left: 28%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .basic {
  width: 100%;
  height: 2.5rem;
  background-color: #e2e2e2;
}
main .basic > div {
  width: 100%;
  height: 2rem;
  background-color: white;
}
main .basic > div p {
  font-size: 0.88rem;
  text-align: left;
  padding-top: 3.5%;
  padding-left: 3%;
  color: black;
}
main .basic > div div {
  width: 23%;
  height: 0.1rem;
  margin-left: 3%;
  margin-top: 1%;
  background-color: #df0001;
}

main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman > div {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman:last-child {
  padding-bottom:0.5rem;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
</style>


